// ===================================================================
// IMPORTS
// ===================================================================

@use "styles/variables" as *;
@use "styles/mixins" as *;

// ===================================================================
// GLOBAL STYLES
// ===================================================================

* {
  box-sizing: border-box;
}

body {
  background-color: $accent-color;
  margin: 0;
  font-family: $font-family-base;
  @include font-smoothing;
}

.text-center {
  @include text-center;
}

.flex-center {
  @include flex-center;
}

.flex-column {
  @include flex-column;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $font-family-base;
  color: $text-color;
  margin: 0;
  line-height: 100%;
}

p {
  font-family: $font-family-base;
  color: $text-color;
  margin: 0;
  line-height: 100%;
}

li {
  list-style: none;
}

a {
  color: $text-color;
  text-decoration: none;
  font-size: 18px;
  font-weight: 700;
  font-family: $font-family-base;
}

// ===================================================================
// GLOBAL MODIFIERS
// ===================================================================

body.mobile-menu-open {
  overflow: hidden;
}

// ===================================================================
// UTILITY CLASSES
// ===================================================================

.container {
  padding: 0 80px;
  margin: 0 auto;
  max-width: 1300px;

  @include tablet {
    padding: 0 30px;
  }

  @include mobile {
    padding: 0 20px;
  }
}

// ===================================================================
// LAYOUT COMPONENTS
// ===================================================================

.main-layout {
  @include flex-column;
  min-height: 100vh;
}

.App {
  @include text-center;
}

// ===================================================================
// HEADER COMPONENT
// ===================================================================

.header {
  @extend .container;

  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  max-width: 100%;
  height: 80px;

  background-color: $background-color;

  @include mobile {
    height: 48px;
  }
}

.header__logo {
  svg {
    height: 24px;
    width: auto;
  }
}

.header__nav {
  @include mobile {
    display: none;
  }
}

.header__nav-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 30px;
}

.header__nav-link {
  position: relative;
  background: transparent;
  background-clip: text;

  &:hover {
    background: linear-gradient(
      98.25deg,
      $secondary-color 18.56%,
      $primary-color 76.02%
    );
    background-clip: text;
    color: transparent;
  }
}

.header__cart-icon {
  .cart-icon-container {
    position: relative;
    display: flex;
    width: 24px;
    height: 24px;
  }

  .cart-icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity 0.3s ease, transform 0.3s ease;

    &.visible {
      opacity: 1;
      transform: scale(1);
    }

    &.hidden {
      opacity: 0;
      transform: scale(0.8);
    }

    &--hover {
      &.visible {
        transform: scale(1.1);
      }
    }
  }
}

.header__burger {
  display: none;

  @include mobile {
    display: block;
  }
}

.header__burger-btn {
  background: none;
  border: none;
  cursor: pointer;
  padding: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.2s ease;

  &:hover {
    transform: scale(1.05);
  }

  &:active {
    transform: scale(0.95);
  }

  svg {
    height: 37px;
    width: auto;
    display: block;
  }
}

// ===================================================================
// HERO SECTION COMPONENT
// ===================================================================

.hero-section {
  position: relative;

  @include flex-column;
  justify-content: space-between;
  margin-bottom: 55px;
  overflow: hidden;

  background-image: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) -0.52%,
      rgba(15, 20, 32, 0.79) 99.48%
    ),
    url("./assets/images//hero-bg.webp");
  background-size: cover, cover;
  background-position: center, center;
  background-repeat: no-repeat, no-repeat;

  @include mobile {
    margin-bottom: 50px;
    max-height: 450px;
    padding-bottom: 40px;
  }

  .container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding-top: 50px;
    padding-bottom: 50px;

    @include mobile {
      justify-content: flex-start;
      flex-direction: column-reverse;
      min-height: 450px;
      padding-bottom: 40px;
    }
  }

  .hero-section__content {
    display: flex;
    flex-direction: column;
    justify-content: start;
    gap: 22px;
    max-width: 650px;
    margin: 0;
    flex-shrink: 0;

    @include mobile {
      align-items: center;
    }
  }

  .hero-section__image {
    display: flex;
    justify-content: end;
    align-items: center;

    svg {
      width: 327px;
      height: 367px;
    }

    @include mobile {
      svg {
        width: 310px;
      }
    }
  }

  .hero-section__title {
    font-weight: 800;
    font-size: 72px;
    line-height: 100%;
    text-align: start;

    @include tablet {
      max-width: 450px;
      font-size: 48px;
    }

    @include mobile {
      font-size: 36px;
      font-weight: 800;
      line-height: 100%;
      text-align: center;
    }
  }

  .hero-section__subtitle {
    font-weight: 800;
    font-size: 32px;
    text-align: start;

    @include tablet {
      font-size: 22px;
    }

    @include mobile {
      text-align: center;
      font-size: 16px;
    }
  }

  .hero-section__button {
    @extend .button;
    @extend .button--primary;

    height: 50px !important;
    max-width: 190px;

    @include mobile {
      height: 33px !important;
    }
  }

  .hero-section__title-span {
    color: $primary-color;
  }

  .hero-section__subtitle-span {
    color: $secondary-color;
  }
}

// ===================================================================
// BUTTON COMPONENT
// ===================================================================

.button {
  padding: 0px 40px;
  border: none;
  border-radius: 100px;
  height: 33px;

  font-family: $font-family-base;
  font-size: 16px;
  font-weight: 700;

  cursor: pointer;

  transition: all 0.3s ease-in-out;

  &:focus {
    outline: none;
  }

  &:hover {
    opacity: 60%;
  }

  &--primary {
    background-color: $secondary-color;
    color: $background-color;
  }

  &--secondary {
    border: 2px solid $secondary-color;
    background-color: transparent;
    color: $secondary-color;
  }
}

// ===================================================================
// OFFER SECTION COMPONENT
// ===================================================================

.offer-section {
  @extend .container;
  @include flex-column;
  margin-bottom: 55px;

  @include mobile {
    margin-bottom: 50px;
  }
}

.offer-section__header {
  align-self: start;
  margin-bottom: 40px;

  @include mobile {
    margin-bottom: 30px;
  }
}

.offer-section__title {
  font-size: 32px;
  font-weight: 800;
  color: $text-color;
  text-align: left;

  @include tablet {
    font-size: 24px;
  }

  @include mobile {
    font-size: 18px;
    text-align: center;
  }
}

.offer-section__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 20px;

  @include tablet {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto;
    height: auto;
  }

  @include mobile-large {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    gap: 15px;
  }

  @include mobile {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    gap: 15px;
  }
}

// ===================================================================
// OFFER CARD COMPONENT
// ===================================================================

.offer-card {
  position: relative;

  border-radius: 10px;
  overflow: hidden;
  display: flex;
  align-items: flex-end;
  min-height: 200px;

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  transition: transform 0.3s ease;

  &:hover {
    transform: translateY(-5px);
  }
}

.offer-card--with-background {
  background-image: linear-gradient(
      180deg,
      rgba(0, 0, 0, 0) -0.52%,
      rgba(15, 20, 32, 0.79) 99.48%
    ),
    var(--background-image);
}

.offer-card--large {
  grid-column: span 2;
  min-height: 350px;

  @include tablet {
    grid-column: span 1;
    min-height: 300px;
  }

  @include mobile {
    grid-column: span 1;
    grid-row: span 1;
    min-height: 250px;
  }
}

.offer-card--medium {
  grid-row: span 1;
  min-height: 280px;

  @include tablet {
    min-height: 200px;
  }

  @include mobile {
    min-height: 200px;
  }
}

.offer-card__content {
  padding: 30px;
  width: 100%;
  z-index: 2;

  @include mobile {
    padding: 20px;
    padding-top: 80px;
  }
}

.offer-card__title {
  font-size: 40px;
  font-weight: 800;
  color: $text-color;
  margin-bottom: 12px;
  line-height: 1.2;
  text-align: left;
  max-width: 635px;

  @include tablet {
    max-width: 200px;
    font-size: 40px;
  }

  @include mobile {
    max-width: 200px;
  }
}

.offer-card--large .offer-card__title {
  @include desktop {
    font-size: 74px;
  }
}

.offer-card--large .offer-card__subtitle {
  @include desktop {
    font-size: 26px;
  }
}

.offer-card--medium .offer-card__title {
  font-size: 46px;
  max-width: 280px;

  @include tablet {
    font-size: 40px;
  }

  @include mobile {
    font-size: 40px;
  }
}

.offer-card__subtitle {
  font-size: 24px;
  font-weight: 400;
  color: $text-color;
  margin-bottom: 22px;
  line-height: 1.4;
  text-align: left;

  @include tablet {
    font-size: 16px;
  }

  @include mobile {
    font-size: 16px;
  }
}

.offer-card__subtitle-main {
}

.offer-card__subtitle-hidden {
  @include tablet {
    display: none;
  }

  @include mobile {
    display: none;
  }
}

.offer-card__button {
  @extend .button;
  @extend .button--secondary;
  padding: 0 30px;
  height: 40px;
  font-size: 14px;
  display: block;

  @include mobile {
    height: 35px;
    padding: 0 25px;
    font-size: 13px;
  }
}

// ===================================================================
// INFO SECTION COMPONENT
// ===================================================================

.info-section {
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: $accent-color;
  color: $text-color;
  margin-bottom: 55px;

  @include mobile {
    margin-bottom: 50px;
  }
}

.info-section__title {
  font-size: 32px;
  font-weight: 800;
  margin-bottom: 17px;
  color: $text-color;
  text-align: left;

  @include tablet {
    font-size: 24px;
  }

  @include mobile {
    font-size: 18px;
  }
}

.info-section__text {
  font-size: 20px;
  line-height: 1.6;
  margin-bottom: 17px;
  text-align: left;
  opacity: 0.6;

  @include tablet {
    font-size: 16px;
  }

  @include mobile {
    font-size: 14px;
  }
}

.info-section__toggle {
  display: none;
}

.info-section__expandable {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.3s ease;
}

.info-section__toggle:checked ~ .info-section__expandable {
  max-height: 500px;
}

.info-section__button {
  padding: 0px !important;
  color: $secondary-color;
  background-color: transparent;
  font-size: 14px;
  font-weight: 700;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: $font-family-base;
  border: none;
  display: block;

  &:hover {
    opacity: 0.6;
  }

  @include tablet {
    padding: 12px 25px;
    font-size: 15px;
  }

  @include mobile {
    padding: 10px 20px;
    font-size: 14px;
    display: block;
    margin: 0 auto;
  }
}

.info-section__button-text {
  &--more {
    display: flex;
  }

  &--less {
    display: none;
  }
}

.info-section__toggle:checked
  ~ .info-section__button
  .info-section__button-text--more {
  display: none;
}

.info-section__toggle:checked
  ~ .info-section__button
  .info-section__button-text--less {
  display: flex;
}

// ===================================================================
// FOOTER COMPONENT
// ===================================================================

.footer {
  padding-bottom: 20px;
  margin-top: auto;

  background-color: $background-color;
}

.footer__content--text {
  font-size: 14px;
  color: $secondary-color;
  text-align: center;
}

// ===================================================================
// MOBILE MENU COMPONENT
// ===================================================================

.mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $background-color;
  z-index: 998;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;

  &--open {
    opacity: 1;
    visibility: visible;
  }
}

.mobile-menu {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 999;

  width: 100%;
  height: 100%;
  @include flex-column;

  background-color: $background-color;
  box-shadow: -2px 0 10px $background-color;

  transform: translateX(100%);
  transition: transform 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);

  &--open {
    transform: translateX(0);
  }
}

.mobile-menu__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 20px;
  min-height: 48px;

  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.mobile-menu__logo {
  svg {
    height: 24px;
    width: auto;
  }
}

.mobile-menu__close {
  border: none;
  padding: 4px;
  font-size: 24px;
  color: $text-color;
  line-height: 1;
  cursor: pointer;
  background: none;
  transition: color 0.2s ease, transform 0.2s ease;

  svg {
    width: 26px;
    height: 26px;
  }

  &:hover {
    color: $primary-color;
    transform: scale(1.1);
  }

  &:active {
    transform: scale(0.95);
  }
}

.mobile-menu__nav {
  flex: 1;
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.mobile-menu__nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.mobile-menu__nav-item {
  margin: 0;

  &:not(:last-child) {
    border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  }
}

.mobile-menu__nav-link {
  display: block;
  padding: 20px 40px;
  color: $text-color;
  text-decoration: none;
  font-size: 24px;
  font-weight: 700;
  font-family: $font-family-base;
  transition: all 0.2s ease;
  position: relative;
  text-align: left;

  &:hover {
    background-color: rgba(255, 255, 255, 0.05);
    padding-left: 44px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 4px;
      background: linear-gradient(
        98.25deg,
        $secondary-color 18.56%,
        $primary-color 76.02%
      );
    }
  }

  &:active {
    background-color: rgba(255, 255, 255, 0.1);
  }
}

.mobile-menu__cart-item {
  margin-top: auto;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
  border-bottom: none;
}

.mobile-menu__cart-wrapper {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 20px 40px;
  color: $text-color;
  font-size: 24px;
  font-weight: 700;
  font-family: $font-family-base;
  transition: all 0.2s ease;
  position: relative;
  text-align: left;
  cursor: pointer;
  background: none;
  border: none;
  width: 100%;
  justify-content: flex-start;

  &:hover {
    background-color: rgba(255, 255, 255, 0.05);
    padding-left: 44px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 4px;
      background: linear-gradient(
        98.25deg,
        $secondary-color 18.56%,
        $primary-color 76.02%
      );
    }
  }

  &:active {
    background-color: rgba(255, 255, 255, 0.1);
  }

  .cart-icon-container {
    position: relative;
    display: flex;
    width: 28px;
    height: 28px;
    flex-shrink: 0;
  }

  .cart-icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    transition: opacity 0.3s ease, transform 0.3s ease;

    &.visible {
      opacity: 1;
      transform: scale(1);
    }

    &.hidden {
      opacity: 0;
      transform: scale(0.8);
    }

    &--hover {
      &.visible {
        transform: scale(1.1);
      }
    }
  }
}

.mobile-menu__cart-text {
  font-size: 24px;
  font-weight: 700;
}
