// Import variables for use in mixins
@use "variables" as *;

// Flexbox Mixins
@mixin flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

@mixin flex-column {
  display: flex;
  flex-direction: column;
}

@mixin flex-center-column {
  @include flex-column;
  @include flex-center;
}

// Font Smoothing Mixin
@mixin font-smoothing {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// Media Query Mixins
@mixin mobile {
  @media (max-width: $mobile) {
    @content;
  }
}

@mixin mobile-large {
  @media (max-width: $mobile-large) {
    @content;
  }
}

@mixin tablet {
  @media (min-width: #{$mobile + 1px}) and (max-width: #{$desktop - 1px}) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: $desktop) {
    @content;
  }
}

// Text Styling Mixins
@mixin text-center {
  text-align: center;
}
